
import { defineComponent, reactive } from "vue";

// 変数の型定義
interface Data {
  isClick: boolean;
}

export default defineComponent({
  setup() {
    // ref(=data)の初期値定義
    //   MEMO: 前リターン -> ref<type || interface> & 後リターン
    //     data: (): Data => {
    //     return {

    // interfaceの場合は`reactive`を使う
    const state = reactive<Data>({
      isClick: false,
    });
    // 単体のdataの場合は`ref`を使う
    //const isClick = ref<boolean>(false);
    return {
      state,
    };
  },
});
